      <div class="row">
        <div class="col-12">
          <form class="form row">
            <div class="col-5">
              <div class="input-group input-lg">
                <div class="input">
                  <input
                  type="text"
                  name="keyowrd"
                  id="keyowrd"
                  class="form-control"
                  v-model="keyword"
                  placeholder="可用身分證、姓名、保單號碼查詢">
                </div>
                <button
                class="btn btn-primary btn-icon"
                type="submit"
                @click.stop.prevent="doSearch()">
                  <i class="icon-search i-24"></i>
                </button>
              </div>
            </div>
            <div class="col-7">
              <div class="form-group text-center mt-1">
                <a class="btn btn-default m-0">列出所有保校家庭</a>
                <span class="text mx-2">或</span>
                <v-select :options="[]" placeholder="依群組查詢" class="v-select-inline" />
                <a class="btn btn-default m-0">查詢</a>
              </div>
            </div>
          </form>
        </div>
      </div>
    </section>
    <div class="container-fluid" v-if="!selected">
      <h4 class="text-green">戶長列表</h4>
      <p>請選擇你想做建議的家庭</p>
      <div class="button-grid five-items mt-2">
        <a class="btn btn-border-green"
        v-for="(cus, i) in customers"
