<script>

// https://tcrm.cathaylife.com.tw/PolicyAdjustmentWebApplication/EntryPage.ashx?HolderID=E12345678A&RunAsID=N13456789A&logintag=

export default {
  name: 'InsuranceCheck',
  data () {
    return {
      selected: null,
      keyword: '',
      frameSrc: "development" == "development" 
                    ? 'https://tcrm.cathaylife.com.tw/PolicyAdjustmentWebApplication/EntryPage.ashx?HolderID=E12345678A&RunAsID=N13456789A&logintag=' 
                    : '',
      customers: [{id: 1, name: '林怡君'}, {id: 2, name: '陳志明'}, {id: 3, name: '陳一明'}, {id: 1, name: '陳名醫'}, {id: 1, name: '林家豪'}, {id: 1, name: '林志明'}, {id: 1, name: '林一明'}, {id: 1, name: '林名醫'}]
    }
  },
  methods: {
    userSelect (i) {
      this.selected = {
        HolderID: 'E12345678A',
        RunAsID: "小○小"
      }
      this.$router.push({
        name: 'insurance-check',
        query: {
          HolderID: 'E12345678A',
          RunAsID: "小○小"
        }
      })
      // window.location.hash = `#/${this.$route.name}?cusid=E12345678A&cusname=小○小`
    },
    detectHash () {
      // 判斷是否有 query
      if(!this.isEmpty(this.$route.query)) {
        // iframe redirect
        this.$router.push({
          path: '/detail/1/tab/1',
          query: {
            cusid: this.$route.query.cusid
          }
        })
      } else {
        // back to initial page
        // this.selected = true
      }
    },
    isEmpty(obj) {
      for(var key in obj) {
        if(obj.hasOwnProperty(key))
          return false;
      }
      return true;
    }
  },
  mounted () {
    this.detectHash()
    window.addEventListener("hashchange", () => {
      this.detectHash()
    }, false);
  }
}
</script>
